import React, { useState, useEffect } from "react";
import { Box, Typography, Grid, CircularProgress, Fade } from "@mui/material";
import MovieCard from "../../components/MovieCard";
import axiosInstance from "../../axiosConfig";
import { useNavigate } from "react-router-dom";

export default function HomePage() {
  const [mediaItems, setMediaItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    const fetchMedia = async () => {
      setLoading(true);
      setError("");

      try {
        const response = await axiosInstance.get("/User/GetMedia");
        setMediaItems(response.data);
      } catch (err) {
        setError(err.response?.data?.error || "Failed to fetch media");
      } finally {
        setLoading(false);
      }
    };

    fetchMedia();
  }, []);

  const movies = mediaItems.filter(
    (item) => item.type && item.type.toLowerCase() === "movie"
  );
  const series = mediaItems.filter(
    (item) => item.type && item.type.toLowerCase() === "series"
  );

  return (
    <Box
      sx={{
        width: "100%",
        minHeight: "100vh",
        bgcolor: "background.default",
        py: 4,
        px: { xs: 2, sm: 3, md: 4 },
      }}
    >
      {/* Loading State */}
      {loading && (
        <Fade in={loading} timeout={400}>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              minHeight: "50vh",
            }}
          >
            <CircularProgress sx={{ color: "primary.main", mb: 2 }} />
            <Typography
              variant="body1"
              sx={{ color: "text.secondary", fontWeight: 500 }}
            >
              Loading your media...
            </Typography>
          </Box>
        </Fade>
      )}

      {/* Error State */}
      {error && !loading && (
        <Fade in={!!error} timeout={400}>
          <Box
            sx={{
              bgcolor: "rgba(255, 75, 75, 0.1)",
              border: "1px solid rgba(255, 75, 75, 0.3)",
              borderRadius: 2,
              p: 2,
              mb: 3,
              textAlign: "center",
            }}
          >
            <Typography
              variant="body1"
              sx={{ color: "error.main", fontWeight: 500 }}
            >
              {error}
            </Typography>
          </Box>
        </Fade>
      )}

      {/* Movies Section */}
      {!loading && (
        <Fade in={!loading} timeout={600}>
          <Box sx={{ mb: 6 }}>
            <Typography
              variant="h4"
              sx={{
                fontWeight: 700,
                color: "text.primary",
                mb: 3,
                letterSpacing: "-0.025em",
              }}
            >
              Movies
            </Typography>
            <Grid container spacing={{ xs: 2, sm: 3 }}>
              {movies.length > 0 ? (
                movies.map((item) => (
                  <Grid item xs={12} sm={6} md={4} lg={3} key={item.mediaId}>
                    <MovieCard
                      title={item.title || "No title"}
                      description={item.description || "-"}
                      posterUrl={
                        item.thumbnailPath ? `/${item.thumbnailPath}` : ""
                      }
                      trailerUrl={
                        item.trailerPath ? `/${item.trailerPath}` : ""
                      }
                      mediaId={item.mediaId}
                    />
                  </Grid>
                ))
              ) : (
                <Typography
                  variant="body1"
                  sx={{ color: "text.secondary", ml: 2 }}
                >
                  No movies found.
                </Typography>
              )}
            </Grid>
          </Box>
        </Fade>
      )}

      {/* Series Section */}
      {!loading && (
        <Fade in={!loading} timeout={600}>
          <Box>
            <Typography
              variant="h4"
              sx={{
                fontWeight: 700,
                color: "text.primary",
                mb: 3,
                letterSpacing: "-0.025em",
              }}
            >
              Series
            </Typography>
            <Grid container spacing={{ xs: 2, sm: 3 }}>
              {series.length > 0 ? (
                series.map((item) => (
                  <Grid item xs={12} sm={6} md={4} lg={3} key={item.mediaId}>
                    <MovieCard
                      title={item.title || "No title"}
                      description={item.description || "-"}
                      posterUrl={
                        item.thumbnailPath ? `/${item.thumbnailPath}` : ""
                      }
                      trailerUrl={
                        item.trailerPath ? `/${item.trailerPath}` : ""
                      }
                      mediaId={item.mediaId}
                    />
                  </Grid>
                ))
              ) : (
                <Typography
                  variant="body1"
                  sx={{ color: "text.secondary", ml: 2 }}
                >
                  No series found.
                </Typography>
              )}
            </Grid>
          </Box>
        </Fade>
      )}
    </Box>
  );
}
